import { Component, OnInit } from '@angular/core';
import { ModalController } from '@ionic/angular';
import { IvtModalPage } from '../ivt-modal/ivt-modal.page';
import { InicioService } from '../../../providers/Almacenamiento/inicio.service';
import { InventariosService } from '../../../providers/Api/Inventarios.service';
import { ToastServices } from '../../../providers/Utilidades/ToastService.service';
import { StatusBar } from '@ionic-native/status-bar/ngx';
import { Router } from '@angular/router';

@Component({
  selector: 'app-ivt-inicio',
  templateUrl: './ivt-inicio.page.html',
  styleUrls: ['./ivt-inicio.page.scss'],
})
export class IvtInicioPage implements OnInit {

  load = false;
  ivts = [];

  constructor(
    private modalCtrl: ModalController,
    private almacenamiento: InicioService,
    private ivtApi: InventariosService,
    private toast: ToastServices,
    private statusbar: StatusBar,
    private router: Router
  ) { }

  ngOnInit() {
  }


  ionViewWillEnter() {
      this.cargar();
  }
  refrescando( event ) {
    this.cargar().then(() => {
      event.target.complete()
    }).catch(() => event.target.complete())
  }

  async cargar() {
    this.statusbar.backgroundColorByHexString('#1E8C4A');
    this.load = true;
    const info: any = await this.almacenamiento.obtenerToken();

    if ( info ) {
        this.ivtApi.leerInventarios(info.token).subscribe((data: any) => {
          if (data.exe) {
              console.log(data.response);
              this.ivts = data.response;
          } else {
            this.toast.toastError('No se pudo crear el inventario, intentelo más tarde');
          }
          this.load = false;
        }, (err) => {
          this.toast.toastError('No se pudo crear el inventario, intentelo más tarde');
          this.load = false;
        })
    }

  }


  async crearInventario() {
    const modal = await this.modalCtrl.create({
      component: IvtModalPage,
      componentProps: {
        tipo: 'crear'
      }
    })

    await modal.present();
  }

  inventario( ivt: any ) {
    this.router.navigate(['/central/menu/ivt-miinventario', ivt._id])
  }

}
